import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { variant } from 'styled-system'
import { BackgroundGradient, Box, Flex, Span } from '@horizin/design-system/dist'
import { CSSTransition } from 'react-transition-group';

const Modal = styled(Box)
`
${
  variant({
    prop: 'position',
    variants: {
      default: {
        transform: 'translate(50%, 50%)',
        left: 0,
        right: 0,
        top: 0,
        bottom: 0,
        height: '50%',
        width: '50%'
      },
      fullScreen: {
        left: 0,
        right: 0,
        top: 0,
        bottom: 0,
        borderRadius: 0,
        // width: '100%',
        // height: '100%',
      },
    }
  })
}
`

Modal.defaultProps = {
  bg: 'white',
  boxShadow: 0,
  fixed: true,
  relative: false,
  position: 'default'
}

const ModalActions = ({ portal, label, content, styled, styledLabel, id, ...props}) => {
  const [isAnimating, setIsAnimating] = useState(false);
  const [isOpening, setIsOpening] = useState(false);

  const closeHandler = () => {
    setIsAnimating(false)
    setTimeout(() => {
      portal.closeModals()
    }, 300);
  }

  useEffect(() => {
    if(!isOpening && !isAnimating) {
      setIsOpening(true)
      setIsAnimating(true)
    }
  }, [isOpening, isAnimating])
  return (
    <>
      <CSSTransition in={(isAnimating)} timeout={300} classNames="fadeIn">
        {
          !isOpening ? <div></div> :
          <BackgroundGradient bg='black' fixed={true} absolute={false} opacity={.2} onClick={closeHandler} />
        }
      </CSSTransition>


      <CSSTransition in={(isAnimating)} timeout={300} classNames="fadeIn">
        {
          !isOpening ? <div></div> :
          <Modal {...styled} {...styled}>
            {
              label &&
              <Flex alignCenter between fullWidth gradient='gray' p={10} {...styledLabel}>
                <Span fontSize={[3]}>{label}</Span>
                <Span pointer md heavy pointer onClick={closeHandler}>X</Span>
              </Flex>
            }
            <Box>
              {
                content && React.isValidElement(content)
                  ? React.cloneElement(content)
                  : content
              }
            </Box>
          </Modal>
        }
      </CSSTransition>
    </>
  )
}

export default ModalActions

